@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
/*===== GOOGLE FONTS =====*/

/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;

  /*========== Colors ==========*/
  --first-color: #38e468;
  --second-color: #048654;
  --title-color: #393939;
  --text-color: #707070;
  --text-color-light: #A6A6A6;
  --body-color: #FBFEFD;
  --container-color: #FFFFFF;

  /*========== Font and typography ==========*/
  --body-font: 'Poppins', sans-serif;
  --biggest-font-size: 2.25rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margins ==========*/
  --mb-1: .5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}

@media screen and (min-width: 768px){
  :root{
    --biggest-font-size: 4rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}
/*========== BASE ==========*/
*,::before,::after{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}
body{
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  background-color: #FBFEFD;
  background-color: var(--body-color);
  color: #707070;
  color: var(--text-color);
  line-height: 1.6;
}

h1,h2,h3,p,ul{
  margin: 0;
}

ul{
  padding: 0;
  list-style: none;
}

a{
  text-decoration: none;
}

img{
  max-width: 100%;
  height: auto;
}
img.naviya-logo {
    width: 150px !important;
}

/*========== CLASS CSS ==========*/
.section{
  padding: 4rem 0 2rem;
}

.section-title, .section-subtitle{
  text-align: center;
}

.section-title{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.section-subtitle{
  display: block;
  color: #38e468;
  color: var(--first-color);
  font-weight: 500;
  font-weight: var(--font-medium);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}
.text-center{
    text-align: center;
}
.mt-10{
    margin-top:10px;
}
/*========== LAYOUT ==========*/
.naviya-container{
  max-width: 1200px;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.bd-grid{
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.l-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #FBFEFD;
  background-color: var(--body-color);
}

/*========== NAV ==========*/
#header{
    transition: all 0.5s;
    z-index: 997;
    padding: 20px 0;
}
.nav{
  max-width: 1024px;
  height: 3rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 768px){
  .nav__menu{
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    padding: 1.5rem 0 1rem;
    text-align: center;
    background-color: #FBFEFD;
    background-color: var(--body-color);
    transition: .4s;
    box-shadow: 0 4px 4px rgba(0,0,0,.1);
    border-radius: 0 0 1rem 1rem;
    z-index: 100;
    z-index: var(--z-fixed);
  }
}

.nav__item{
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.nav__link, .nav__logo, .nav__toggle{
  color: #707070;
  color: var(--text-color);
  font-weight: 500;
  font-weight: var(--font-medium);
}

.nav__logo:hover{
  color: #38e468;
  color: var(--first-color);
}


.nav__link{
  transition: .3s;
}

.nav__link:hover{
  color: #38e468;
  color: var(--first-color);
}

.nav__toggle{
  font-size: 1.3rem;
  cursor: pointer;
}

/* Show menu */
.show-menu{
  top: 3rem;
  top: var(--header-height);
}

/* Active menu */
.active-link{
  color: #38e468;
  color: var(--first-color);
}

/* Change background header */
.scroll-header{
  box-shadow: 0 2px 4px rgba(0,0,0,.1);
}

/* Scroll top */
.scrolltop{
  position: fixed;
  right: 1rem;
  bottom: -20%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: .3rem;
  background: rgba(6,156,84,.5);
  border-radius: .4rem;
  z-index: 10;
  z-index: var(--z-tooltip);
  transition: .4s;
  visibility: hidden;
}

.scrolltop:hover{
  background-color: var(--first-color-alt);
}

.scrolltop__icon{
  font-size: 1.8rem;
  color: #FBFEFD;
  color: var(--body-color);
}

/* Show scrolltop */
.show-scroll{
  visibility: visible;
  bottom: 1.5rem;
}

/*========== HOME ==========*/

#home{
    width: 100%;
    background-image: url(/naviya-react/static/media/hero-bg.5ef60beb.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right top;
    margin-top: 70px;
    padding: 60px 0;
}
.home__container{
  height: calc(100vh - 3rem);
  height: calc(100vh - var(--header-height));
  align-content: center;
}

.text-gra{
    font-size: 1rem;
    font-size: var(--h3-font-size);
    background: -webkit-linear-gradient(#84f9a7, #05cbfb);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.home__title{
  color: #38e468;
  color: var(--first-color);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.home__subtitle{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.home__img{
  width: 300px;
  justify-self: center;
}
@media (min-width: 1024px){
#home{
    background-attachment: fixed;
}
}
/*========== BUTTONS ==========*/
.button{
  display: inline-block;
  background-image: linear-gradient(to right, #92fe9d 0%, #00c9ff 100%);
  color: #000;
  padding: .75rem 1rem;
  border-radius: .5rem;
  transition: .3s;
}

.btn-get-start{
        padding: 10px 40px;
        border-radius: 30px;
        margin-top: 10px;
}
.button:hover{
  background-color: var(--first-color-alt);
}

/*========== ABOUT ==========*/
.about__data{
  text-align: center;
}

.about__description{
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
  text-align: justify;
}

.about__img{
  width: 280px;
  border-radius: .5rem;
  justify-self: center;
}

/*========== SERVICES ==========*/
.services__content{
  text-align: center;
}

.services__img{
  width: 64px;
  height: 64px;
  fill: #38e468;
  fill: var(--first-color);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.services__title{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.services__description{
  padding: 0 1.5rem;
}


/*---Products & solutions ---*/
#products {
    background: linear-gradient(rgba(2, 2, 2, 0.5), rgba(0, 0, 0, 0.5)), url(/naviya-react/static/media/cta-bg.edececea.jpg) fixed center center;
    background-size: cover;
    padding: 120px 0;
}
.text-white{
    color:#fff !important;
}
.box-area{
    display:flex;
    flex-wrap:wrap;
    justify-content: center;
    align-items: center;
}
.single-box{
    position: relative;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:300px;
    height:400px;
    border-radius: 5px;
    margin:20px;
    padding:20px;
    transition: .3s;
}
.single-box:nth-child(1){
   
        background-image: linear-gradient(to right, #92fe9d 0%, #00c9ff 100%);
}
.single-box:nth-child(3){
    background: linear-gradient(
        -180deg
        , #BCC5CE 0%, #929EAD 98%), radial-gradient(at top left, rgba(255, 255, 255, 0.30) 0%, rgba(0, 0, 0, 0.30) 100%);
            background-blend-mode: screen;
}
.single-box:nth-child(2){
    background-image: linear-gradient(to top, #accbee 0%, #e7f0fd 100%);
}
/*-- Contact Form ---*/
.form-container{
    padding:30px;
    background-color: #ffffff;
    box-shadow: 1px 2px 3px #d0cece;
}
.contact__container {
    grid-template-columns: 1.75fr 1fr;
    align-items: center;
    padding-top: 10px;
}
.row{
    width:100%;
    display:grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap:20px 30px;
    margin-bottom: 20px;
}
label{
    color: #1c093c;
    font-size: 14px;
}
textarea,
input{
    width: 100%;
    font-weight: 400;
    padding: 8px 10px;
    border-radius: 5px;
    border: 1.2px solid #c4cae0;
    margin-top: 5px;
}
textarea{
    resize: none;
}
textarea:focus,
input:focus{
    outline: none;
    border-color:#38e468;
    border-color:var(--first-color);
}
button{
    border: none;
    padding: 10px 20px;
    background-image: linear-gradient(to right, #92fe9d 0%, #00c9ff 100%);
    color: #ffffff;
    border-radius: 3px;
}
.info-box {
    color: #444444;
    text-align: center;
    box-shadow: 0 0 30px rgb(214 215 216 / 60%);
    padding: 20px 0 30px 0;
    background: #fff !important;
}
/*========== FOOTER ==========*/
#footer {
    color: #ffffff;
    font-size: 14px;
    background: #051416;
    box-shadow: 0px 2px 15px rgb(0 0 0 / 10%);
}
.footer__container{
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.footer__logo{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #38e468;
  color: var(--first-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

#footer .footer__content h3{
    font-size: 16px;
    font-weight: bold;
    color: #8afba3;
    position: relative;
    padding-bottom: 12px;
}
.footer__content .social-links a{
    font-size: 18px;
    display: inline-block;
    background: #deddec;
    color: #434175;
    line-height: 1;
    padding: 10px 5px;
    margin-right: 4px;
    margin-top:5px;
    border-radius: 50%;
    text-align: center;
    width: 36px;
    height: 36px;
    transition: 0.3s;
}
.footer__description{
  display: block;
  font-size: .813rem;
  font-size: var(--small-font-size);
  margin: .25rem 0 1.5rem;
  margin: .25rem 0 var(--mb-3);
}

.footer__social{
  font-size: 1.5rem;
  color: #393939;
  color: var(--title-color);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.footer__title{
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.footer__link{
  display: inline-block;
  color: #707070;
  color: var(--text-color);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.footer__link:hover{
  color: #38e468;
  color: var(--first-color);
}

.footer__copy{
  text-align: center;
  font-size: .813rem;
  font-size: var(--small-font-size);
  color: #A6A6A6;
  color: var(--text-color-light);
  margin-top: 3.5rem;
}

svg.svg-inline--fa.fa-envelope.fa-w-16 {
  position: relative;
  left: -7px;
}
svg.svg-inline--fa.fa-phone.fa-w-16 {
  position: relative;
  left: -6px;
}
/*========== MEDIA QUERIES ==========*/
@media screen and (min-width: 576px){
  .home__container,
  .about__container{
    grid-template-columns: repeat(2,1fr);
    align-items: center;
  }

  .about__data, .about__initial,
  .contact__container{
    text-align: left;
    text-align: initial;
  }

  .about__img, .app__img{
    width: 380px;
    order: -1;
  }

  .contact__container{
    grid-template-columns: 1.75fr 1fr;
    align-items: center;
  }
  .contact__button{
    justify-self: center;
  }
}

@media screen and (min-width: 768px){
  body{
    margin: 0;
  }

  .section{
    padding-top: 8rem;
  }

  .nav{
    height: calc(3rem + 1.5rem);
    height: calc(var(--header-height) + 1.5rem);
  }
  .nav__list{
    display: flex;
  }
  .nav__item{
    margin-left: 2.5rem;
    margin-left: var(--mb-5);
    margin-bottom: 0;
  }
  .nav__toggle{
    display: none;
  }

  .change-theme{
    position: static;
    position: initial;
    margin-left: 1rem;
    margin-left: var(--mb-2);
  }

  .home__container{
    height: 100vh;
    justify-items: center;
  }

  .services__container,
  .menu__container{
    margin-top: 3rem;
    margin-top: var(--mb-6);
  }

  .menu__container{
    grid-template-columns: repeat(3, 210px);
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
  }
  .menu__content{
    padding: 1.5rem;
  }
  .menu__img{
    width: 130px;
  }

  .app__store{
    margin: 0 .5rem 0 0;
    margin: 0 var(--mb-1) 0 0;
  }
}

@media screen and (min-width: 960px){
  .naviya-container{
    margin-left: auto;
    margin-right: auto;
  }

  .home__img{
    width: 500px;
  }

  .about__container,
  .app__container{
    grid-column-gap: 7rem;
    -webkit-column-gap: 7rem;
            column-gap: 7rem;
  }
}

/* For tall screens on mobiles y desktop*/
@media screen and (min-height: 721px) {
    .home__container {
        height: 640px;
    }
}

